/* Import modern fonts */
@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@400;700;800;900&family=Rajdhani:wght@300;400;500;600;700&family=Exo+2:wght@100;200;300;400;500;600;700;800;900&family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap');

/* Main Container */
.eventsContainer {
  min-height: 100vh;
  background: linear-gradient(135deg, #0a0a0a 0%, #1a1a2e 50%, #16213e 100%);
  position: relative;
  overflow-x: hidden;
}

/* Enhanced Reverse Button */
.reverseButton {
  position: absolute;
  top: 6px;
  right: 6px;
  width: 38px;
  height: 38px;
  border: none;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.95);
  color: #4982f6;
  font-size: 1.4rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  z-index: 10;
  box-shadow: 0 3px 12px rgba(0, 0, 0, 0.6);
  border: 2px solid rgba(74, 130, 246, 0.5);
  font-weight: bold;
}

.reverseButton:hover {
  background: #4982f6;
  color: white;
  transform: scale(1.2) rotate(-90deg);
  box-shadow: 0 4px 16px rgba(74, 130, 246, 0.4);
  border-color: rgba(255, 255, 255, 0.8);
}

.reverseButton:active {
  transform: scale(1.1) rotate(-45deg);
}

.eventsContainer::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(90deg, transparent 24px, rgba(74, 130, 246, 0.03) 24px, rgba(74, 130, 246, 0.03) 25px, transparent 25px), linear-gradient(180deg, transparent 24px, rgba(74, 130, 246, 0.03) 24px, rgba(74, 130, 246, 0.03) 25px, transparent 25px);
  background-size: 50px 50px;
  pointer-events: none;
}

/* Hero Section - Fixed Background with Top Priority */
.heroSection {
  position: relative;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  margin-bottom: 0;
  padding-bottom: 0;
}

.heroBackground {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    linear-gradient(135deg, rgba(74, 130, 246, 0.25) 0%, rgba(74, 130, 246, 0.15) 100%),
    url('events-hero3.jpg') center/cover no-repeat;
  z-index: 1;
  background-position: center top;
  background-attachment: scroll;
}

.heroBackground::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(
    135deg,
    rgba(10, 10, 10, 0.75) 0%,
    rgba(26, 26, 46, 0.65) 50%,
    rgba(22, 33, 62, 0.8) 100%
  );
}

.heroContent {
  position: relative;
  z-index: 2;
  text-align: center;
  color: white;
  max-width: 800px;
  padding: 0 20px;
}

/* Modern, Techy Font Styles */
.heroTitle {
  font-size: 5.5rem;
  font-weight: 900;
  margin-bottom: 1.5rem;
  background: linear-gradient(135deg, #4982f6 0%, #6c8eff 20%, #a3bffa 50%, #6c8eff 80%, #4982f6 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  background-size: 300% 300%;
  text-shadow: 0 4px 25px rgba(74, 130, 246, 0.6), 0 8px 40px rgba(74, 130, 246, 0.4), 0 0 30px rgba(74, 130, 246, 0.3);
  font-family: 'Orbitron', 'Rajdhani', sans-serif;
  letter-spacing: 4px;
  line-height: 1;
  text-transform: uppercase;
  animation: textShine 4s ease-in-out infinite, textPulse 2s ease-in-out infinite;
  position: relative;
}

.heroTitle::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 200px;
  height: 3px;
  background: linear-gradient(90deg, transparent, #4982f6, transparent);
  animation: linePulse 3s ease-in-out infinite;
}

@keyframes textShine {
  0%, 100% {
    background-position: 0% 50%;
    text-shadow: 0 4px 25px rgba(74, 130, 246, 0.6), 0 8px 40px rgba(74, 130, 246, 0.4), 0 0 30px rgba(74, 130, 246, 0.3);
  }
  50% {
    background-position: 100% 50%;
    text-shadow: 0 4px 30px rgba(74, 130, 246, 0.8), 0 8px 50px rgba(74, 130, 246, 0.6), 0 0 40px rgba(74, 130, 246, 0.4);
  }
}

@keyframes textPulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.02);
  }
}

@keyframes linePulse {
  0%, 100% {
    opacity: 0.5;
    width: 200px;
  }
  50% {
    opacity: 1;
    width: 250px;
  }
}

.heroSubtitle {
  font-size: 2rem;
  margin-bottom: 2.5rem;
  color: #f7fafc;
  font-weight: 600;
  letter-spacing: 3px;
  text-shadow: 0 2px 15px rgba(0, 0, 0, 0.7), 0 0 20px rgba(74, 130, 246, 0.3);
  font-family: 'Rajdhani', 'Exo 2', sans-serif;
  line-height: 1.3;
  text-transform: uppercase;
  position: relative;
  animation: subtitleGlow 3s ease-in-out infinite alternate;
}

@keyframes subtitleGlow {
  0% {
    text-shadow: 0 2px 15px rgba(0, 0, 0, 0.7), 0 0 20px rgba(74, 130, 246, 0.3);
  }
  100% {
    text-shadow: 0 2px 20px rgba(0, 0, 0, 0.7), 0 0 30px rgba(74, 130, 246, 0.5), 0 0 40px rgba(74, 130, 246, 0.3);
  }
}

.heroRegisterBtn {
  display: inline-block;
  padding: 20px 55px;
  background: linear-gradient(135deg, #4982f6 0%, #6c8eff 100%);
  color: white;
  text-decoration: none;
  border-radius: 12px;
  font-weight: 800;
  font-size: 1.4rem;
  transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  border: 3px solid rgba(74, 130, 246, 0.3);
  box-shadow: 0 10px 30px rgba(74, 130, 246, 0.5), 0 0 0 2px rgba(74, 130, 246, 0.2), inset 0 2px 10px rgba(255, 255, 255, 0.2);
  text-transform: uppercase;
  letter-spacing: 2px;
  position: relative;
  overflow: hidden;
  font-family: 'Rajdhani', 'Montserrat', sans-serif;
}

.heroRegisterBtn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: left 0.6s ease;
}

.heroRegisterBtn:hover::before {
  left: 100%;
}

.heroRegisterBtn:hover {
  transform: translateY(-4px) scale(1.05);
  box-shadow: 0 20px 40px rgba(74, 130, 246, 0.6), 0 0 0 3px rgba(74, 130, 246, 0.4), inset 0 2px 15px rgba(255, 255, 255, 0.3);
  background: linear-gradient(135deg, #3a72e6 0%, #5b7eff 100%);
  letter-spacing: 3px;
}

.heroRegisterBtn:active {
  transform: translateY(-2px) scale(1.02);
}

/* Events Grid Section */
.eventsGridSection {
  padding: 0 0 5rem 0;
  position: relative;
  margin-top: 0;
}

.eventsGridSection::before {
  content: '🔁Click sport card to view contacts🔁';
  display: block;
  text-align: center;
  color: #4982f6;
  font-size: 1.1rem;
  margin-bottom: 2rem;
  padding: 15px 20px;
  background: rgba(74, 130, 246, 0.1);
  border-radius: 10px;
  border: 1px solid rgba(74, 130, 246, 0.2);
  animation: pulseGlow 4s ease-in-out infinite;
  backdrop-filter: blur(10px);
  margin-top: 0;
}

@keyframes pulseGlow {
  0%, 100% { 
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  }
  50% { 
    box-shadow: 0 8px 25px rgba(74, 130, 246, 0.2);
  }
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.sectionHeader {
  text-align: center;
  margin-bottom: 4rem;
}

.sectionTitle {
  font-size: 3.5rem;
  font-weight: 800;
  color: #4982f6;
  margin-bottom: 1rem;
  font-family: 'Orbitron', 'Rajdhani', sans-serif;
  text-transform: uppercase;
  letter-spacing: 3px;
  text-shadow: 0 4px 20px rgba(74, 130, 246, 0.4), 0 0 25px rgba(74, 130, 246, 0.2);
  position: relative;
  animation: sectionTitleGlow 4s ease-in-out infinite;
}

@keyframes sectionTitleGlow {
  0%, 100% {
    text-shadow: 0 4px 20px rgba(74, 130, 246, 0.4), 0 0 25px rgba(74, 130, 246, 0.2);
  }
  50% {
    text-shadow: 0 4px 25px rgba(74, 130, 246, 0.6), 0 0 35px rgba(74, 130, 246, 0.3);
  }
}

.sectionSubtitle {
  font-size: 1.5rem;
  color: #e2e8f0;
  font-weight: 500;
  letter-spacing: 2px;
  font-family: 'Rajdhani', 'Exo 2', sans-serif;
  text-transform: uppercase;
}

/* -----------------------------
   FLEX-BASED EVENTS GRID (NEW)
   Replaces grid approach so last row centers
   ----------------------------- */

/* Wrapper that centers the events grid block */
.eventsGridContainer {
  width: 100%;
  display: flex;
  justify-content: center;
}

/* Flex layout: wraps and centers last row naturally */
.eventsGrid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; /* this centers the last, partial row */
  align-items: flex-start;
  gap: 2.5rem;
  padding: 0 1rem;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  box-sizing: border-box;
}

/* Each card is responsive via flex-basis */
.flipCard {
  flex: 1 1 320px;    /* grow | shrink | basis */
  max-width: 350px;
  min-width: 260px;
  height: 400px;
  background-color: transparent;
  perspective: 1000px;
  cursor: pointer;
  box-sizing: border-box;
  margin: 0; /* spacing handled by gap */
}

/* Keep existing inner/card styles (unchanged) */
.flipCardInner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.3);
  border-radius: 20px;
}

.flipCard.flipped .flipCardInner {
  transform: rotateY(180deg);
}

.flipCardFront, .flipCardBack {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border-radius: 20px;
  padding: 2.5rem 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
}

.flipCardFront {
  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(15px);
  border: 2px solid rgba(74, 130, 246, 0.15);
  color: white;
  transition: all 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  overflow: hidden;
  font-family: 'Exo 2', 'Montserrat', sans-serif;
}

.flipCardFront::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(74, 130, 246, 0.15), transparent);
  transition: left 0.6s ease;
}

.flipCard:hover .flipCardFront::before {
  left: 100%;
}

.flipCard:hover .flipCardFront {
  transform: scale(1.03);
  background: rgba(255, 255, 255, 0.08);
  border-color: rgba(74, 130, 246, 0.4);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.4), 0 0 0 2px rgba(74, 130, 246, 0.2), 0 0 30px rgba(74, 130, 246, 0.1);
}

/* Hide scrollbar but keep functionality */
.flipCardBack {
  overflow-y: auto;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.flipCardBack::-webkit-scrollbar {
  display: none;
}

/* Back Side Content - Fixed Header */
.flipCardBack {
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 100%);
  border: 3px solid rgba(74, 130, 246, 0.6);
  color: white;
  transform: rotateY(180deg);
  padding: 0;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

/* Front Side Content */
.eventImageContainer {
  width: 160px;
  height: 160px;
  margin: 0 auto 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 50%;
  padding: 30px;
  transition: all 0.4s ease;
  border: 2px solid rgba(74, 130, 246, 0.2);
}

.flipCard:hover .eventImageContainer {
  background: rgba(74, 130, 246, 0.25);
  transform: scale(1.15) rotate(5deg);
  border-color: rgba(74, 130, 246, 0.5);
  box-shadow: 0 0 30px rgba(74, 130, 246, 0.3);
}

.eventImage {
  width: 140px;
  height: 140px;
  object-fit: contain;
  transition: all 0.4s ease;
}

.flipCard:hover .eventImage {
  transform: scale(1.1);
  filter: drop-shadow(0 0 12px rgba(74, 130, 246, 0.7));
}

.eventName {
  color: white;
  font-size: 1.8rem;
  font-weight: 700;
  font-family: 'Rajdhani', 'Exo 2', sans-serif;
  transition: all 0.3s ease;
  letter-spacing: 1px;
  text-transform: uppercase;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.5);
  margin-bottom: 1.5rem;
}

.flipCard:hover .eventName {
  color: #4982f6;
  text-shadow: 0 0 15px rgba(74, 130, 246, 0.5), 0 0 25px rgba(74, 130, 246, 0.3);
  letter-spacing: 2px;
}

.contactsLink {
  display: inline-block;
  padding: 12px 24px;
  background: linear-gradient(135deg, #4982f6 0%, #6c8eff 100%);
  color: white;
  text-decoration: none;
  border-radius: 8px;
  font-weight: 600;
  font-size: 1rem;
  transition: all 0.3s ease;
  border: 2px solid rgba(255, 255, 255, 0.2);
  font-family: 'Rajdhani', sans-serif;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.contactsButton {
  background: transparent;
  border: 1px solid #007bff;
  color: #007bff;
  padding: 6px 12px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: all 0.3s ease;
}

.contactsButton:hover {
  background: linear-gradient(135deg, #3a72e6 0%, #5b7eff 100%);
  transform: translateY(-2px);
  box-shadow: 0 10px 20px rgba(74, 130, 246, 0.4);
}

.ruleBookLink {
  margin-top: 1rem;
}

.ruleBookLink a {
  display: inline-block;
  padding: 12px 24px;
  background: linear-gradient(135deg, #4982f6 0%, #6c8eff 100%);
  color: white;
  text-decoration: none;
  border-radius: 8px;
  font-weight: 600;
  font-size: 1rem;
  transition: all 0.3s ease;
  border: 2px solid rgba(255, 255, 255, 0.2);
  font-family: 'Rajdhani', sans-serif;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.ruleBookLink a:hover {
  background: linear-gradient(135deg, #3a72e6 0%, #5b7eff 100%);
  transform: translateY(-2px);
  box-shadow: 0 10px 20px rgba(74, 130, 246, 0.4);
}

/* Back Side Content */
.contactsHeader {
  font-size: 2rem;
  font-weight: 800;
  color: #4982f6;
  padding: 1.5rem 2rem 1rem;
  font-family: 'Orbitron', 'Rajdhani', sans-serif;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-shadow: 0 2px 10px rgba(74, 130, 246, 0.4);
  flex-shrink: 0;
  border-bottom: 2px solid rgba(74, 130, 246, 0.3);
  margin: 0;
}

.captainsContainer {
  width: 100%;
  text-align: left;
  max-width: 100%;
  box-sizing: border-box;
  padding: 0 2rem 2rem;
  overflow-y: auto;
  flex-grow: 1;
}

.captainsContainer {
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.captainsContainer::-webkit-scrollbar {
  display: none;
}

.captainDetails {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
  border: 1px solid rgba(74, 130, 246, 0.2);
  backdrop-filter: blur(10px);
  word-wrap: break-word;
  overflow-wrap: break-word;
  max-width: 100%;
}

.captainName {
  font-size: clamp(1rem, 2.5vw, 1.3rem);
  font-weight: 700;
  color: #4982f6;
  margin-bottom: 1rem;
  font-family: 'Rajdhani', sans-serif;
  text-transform: uppercase;
  letter-spacing: 1px;
  line-height: 1.3;
}

.captainInfo {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.captainInfo span {
  font-size: clamp(0.8rem, 2vw, 1rem);
  color: #e2e8f0;
  font-family: 'Exo 2', sans-serif;
  font-weight: 500;
  line-height: 1.4;
  word-break: break-word;
}

.roleDivider {
  font-size: 1.2rem;
  font-weight: 700;
  color: #ffd700;
  margin: 1.5rem 0 1rem;
  padding: 0.5rem;
  background: rgba(255, 215, 0, 0.1);
  border-radius: 6px;
  text-align: center;
  font-family: 'Rajdhani', sans-serif;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.flipHint {
  position: absolute;
  bottom: 1rem;
  left: 0;
  right: 0;
  text-align: center;
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.7);
  font-style: italic;
  font-family: 'Exo 2', sans-serif;
}

/* ---------- Responsive overrides for flex-grid (ensures consistent sizing) ---------- */

@media (min-width: 1201px) {
  .eventsGrid { max-width: 1400px; gap: 2.5rem; }
  .flipCard { flex-basis: 320px; max-width: 350px; height: 400px; }
}

@media (max-width: 1200px) {
  .container { max-width: 1000px; }
  .eventsGrid { max-width: 1000px; gap: 2rem; }
  .flipCard { flex-basis: 300px; max-width: 320px; height: 380px; }
}

@media (max-width: 1024px) {
  .heroTitle { font-size: 4.5rem; }
  .sectionTitle { font-size: 3rem; }
  .eventsGrid { gap: 1.8rem; }
  .flipCard { flex-basis: 280px; width: auto; height: 380px; min-width: 240px; }
}

@media (max-width: 768px) {
  .heroTitle { font-size: 3.5rem; letter-spacing: 3px; }
  .eventsGridSection { padding: 0 0 3rem 0; }
  .eventsGridSection::before { font-size: 1rem; padding: 12px 15px; margin-bottom: 1.5rem; border-radius: 0 0 12px 12px; }
  .heroSubtitle { font-size: 1.6rem; letter-spacing: 2px; }
  .heroBackground { background-position: center 30%; }
  .eventsGrid { gap: 1.5rem; padding: 0 0.5rem; }
  .reverseButton { top: 6px; right: 6px; width: 32px; height: 32px; font-size: 1.1rem; }
  .flipCard { height: 350px; min-width: 220px; }
  .eventImageContainer { width: 140px; height: 140px; padding: 25px; }
  .eventImage { width: 90px; height: 90px; }
  .eventName { font-size: 1.4rem; }
  .sectionTitle { font-size: 2.5rem; }
  .heroRegisterBtn { padding: 18px 40px; font-size: 1.2rem; }
  .captainDetails { padding: 1rem; }
  .captainName { font-size: 1.1rem; }
  .contactsHeader { font-size: 1.6rem; padding: 1.2rem 1.5rem 0.8rem; }
  .captainsContainer { padding: 0 1.5rem 1.5rem; }
}

@media (max-width: 640px) {
  .eventsGrid { gap: 1.5rem; max-width: 400px; margin: 0 auto; }
  .flipCard { flex-basis: 100%; max-width: 100%; height: 340px; min-width: unset; }
  .heroTitle { font-size: 3rem; }
  .heroSubtitle { font-size: 1.4rem; }
  .sectionTitle { font-size: 2.2rem; }
}

@media (max-width: 480px) {
  .eventImageContainer { width: 130px; height: 130px; padding: 22px; }
  .eventImage { width: 85px; height: 85px; }
  .heroSection { height: 60vh; }
  .captainDetails { padding: 1rem; margin-bottom: 1rem; }
  .captainInfo span { font-size: 0.85rem; line-height: 1.5; }
  .contactsHeader { font-size: 1.4rem; padding: 1rem 1.2rem 0.8rem; }
  .flipCardBack { padding: 1.5rem 1rem; }
  .heroTitle { font-size: 2.5rem; letter-spacing: 2px; }
  .heroSubtitle { font-size: 1.3rem; letter-spacing: 1.5px; }
  .eventsGrid { gap: 1.2rem; padding: 0 0.5rem; }
  .eventsGridSection { padding: 0 0 2rem 0; }
  .eventsGridSection::before { font-size: 0.9rem; padding: 10px 12px; margin-bottom: 1rem; border-radius: 0 0 10px 10px; }
  .sectionTitle { font-size: 2rem; }
  .heroRegisterBtn { padding: 16px 35px; font-size: 1.1rem; }
  .flipCard { height: 320px; }
  .container { padding: 0 15px; }
}

@media (max-width: 360px) {
  .heroTitle { font-size: 2.2rem; }
  .heroSubtitle { font-size: 1.1rem; }
  .eventsGrid { gap: 1rem; }
  .flipCard { height: 300px; }
  .eventImageContainer { width: 120px; height: 120px; padding: 20px; }
  .eventImage { width: 80px; height: 80px; }
  .eventName { font-size: 1.2rem; }
  .heroRegisterBtn { padding: 14px 30px; font-size: 1rem; }
}
